@import '../../variables.scss';

.id-header {
  grid-row: 1 / 2;
  grid-column: full-start / full-end;

  position: relative;

  @media only screen and (max-width: $bp-medium) {
    height: 60rem;
  }

  @media only screen and (max-width: $bp-small) {
    height: 50rem;
  }

  @media only screen and (max-width: $bp-smallest) {
    height: 45rem;
  }

  &__image-container {
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-position: 50% 50%;
      background-size: cover;
      border-radius: inherit; /* added to prevent overflow  */
      overflow: hidden; /* added to prevent overflow  */
      background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
      background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 1) 100%
      ); /* W3C */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#825b00',GradientType=0 ); /* IE6-9 */
    }

    &--image {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;

      opacity: 0.5;
    }
  }

  &__content {
    display: flex;
    // flex-direction: column;
    // align-items: flex-end;

    position: absolute;

    bottom: 0;

    z-index: 999;

    font-size: 2rem;

    padding: 10rem;

    @media only screen and (max-width: $bp-medium) {
      padding: 5rem;
    }

    @media only screen and (max-width: $bp-small) {
      font-size: 1.75rem;
    }

    @media only screen and (max-width: $bp-smallest) {
      padding: 5rem 2rem;
    }

    @media only screen and (max-width: $bp-phone) {
      font-size: 1.5rem;
    }

    &--col-1 {
      &--image {
        width: 15rem;
        height: 20rem;
        background-size: cover;
        background-position: center;

        border: 2px solid $color-grey-dark-3;

        margin-right: 3rem;

        @media only screen and (max-width: $bp-phone) {
          width: 12.5rem;
          height: 17.5rem;
        }
      }
    }

    &--col-2 {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      margin-bottom: 1rem;

      &--title {
        font-weight: 500;

        font-size: 3rem;

        @media only screen and (max-width: $bp-small) {
          font-size: 2.5rem;
        }

        @media only screen and (max-width: $bp-phone) {
          font-size: 2rem;
        }
      }

      &--subtitle {
      }

      &--links {
      }
    }
  }
}
